import { useMeta } from 'vue-meta'

const metaSetting = {
  computed: {
    routeName () {
      return this.$route.name
    }
  },
  mounted () {
    useMeta({
      title: '  - ' + `${this.routeName}` + ' ',
      meta: [
        { vmid: 'description', name: 'description', content: `${this.routeName}` + 'Page' },
        { vmid: 'keywords', name: 'keywords', content: 'javascript, vue, html, vue.js, nuxt.js, node.js, ' }
      ]
    })
    console.log('routeName', this.routeName)
  }
}

export default metaSetting
